<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">订单列表</a></el-breadcrumb-item>
      <el-breadcrumb-item>发货</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <div class="from">
        <p>
          收货地址：
          <i v-if="receiverDeliverDtlDataEdit === 1" class="el-icon-edit" @click="receiverDeliverDtlDataEdit = 2"></i>
        </p>
        <div>
          <el-input
            v-if="receiverDeliverDtlDataEdit === 2"
            v-model="deliverDtlData.receiverName"
            placeholder="请输入姓名"
            style="width: 200px"
          ></el-input>
          <span v-else class="accept_name">{{ deliverDtlData.receiverName }}</span>
          <el-input
            v-if="receiverDeliverDtlDataEdit === 2"
            v-model="deliverDtlData.receiverPhone"
            placeholder="请输入手机号"
            style="width: 200px"
          ></el-input>
          <span v-else>{{ deliverDtlData.receiverPhone }}</span><div class="mb-1"></div>
        </div>
        <areaSelectCmp v-if="false" :area="receiverArea" @childFn="onReceiverChild"></areaSelectCmp>
        <el-input
          v-if="receiverDeliverDtlDataEdit === 2"
          v-model="deliverDtlData.receiverAddress"
          placeholder="请输入地址"
          style="width: 400px"
        ></el-input>
        <p v-else>{{ deliverDtlData.receiverAddress }}</p><br>
        <el-button
          v-if="receiverDeliverDtlDataEdit === 2"
          type="primary"
          size="small"
          @click="onReceiverDeliverDtlDataEdit"
        >保存</el-button>
        <p class="send_title">发货地址：<i v-if="deliveryDeliverDtlDataEdit === 1" class="el-icon-edit" @click="deliveryDeliverDtlDataEdit = 2"></i></p>
        <div>
          <el-input v-if="deliveryDeliverDtlDataEdit === 2" v-model="deliverDtlData.deliveryName" placeholder="请输入姓名" style="width: 200px"></el-input>
          <span v-else class="send_name">{{ deliverDtlData.deliveryName }}</span>
          <el-input v-if="deliveryDeliverDtlDataEdit === 2" v-model="deliverDtlData.deliveryPhone" placeholder="请输入手机号" style="width: 200px"></el-input>
          <span v-else>{{ deliverDtlData.deliveryPhone }}</span><div class="mb-1"></div>
        </div>
        <areaSelectCmp v-if="false" :area="deliveryArea" @childFn="onDeliveryChild"></areaSelectCmp>
        <el-input v-if="deliveryDeliverDtlDataEdit === 2" v-model="deliverDtlData.deliveryAddress" placeholder="请输入地址" style="width: 400px"></el-input>
        <p v-else>{{ deliverDtlData.deliveryAddress }}</p><br>
        <el-button v-if="deliveryDeliverDtlDataEdit === 2" type="primary" size="small" @click="onDeliveryDeliverDtlDataEdit">保存</el-button><div class="mb-4"></div>
        <span>请选择物流方式：</span>
        <el-select v-model="logisticValue" placeholder="请选择" size="mini" @change="onSelect">
          <el-option label="二四半日达" :value="0"></el-option>
          <!-- <el-option label="二四即时达" :value="1"></el-option> -->
          <el-option label="三方快递" :value="2"></el-option>
          <el-option label="商家自己配送" :value="3"></el-option>
        </el-select>
        <div v-if="isExpress" class="express_company">
          <span>物流公司</span>
          <el-input v-model="logisticsCompany" class="express_input" size="mini"></el-input>
        </div>
        <div v-if="isExpress" class="express_id">
          <span>货运单号</span>
          <el-input v-model="logistics" class="express_input" size="mini"></el-input>
        </div>
      </div>

      <div class="btn_group">
        <el-button type="success" size="medium" style="width: 100px" round @click="onConfirm">确认发货</el-button>
        <router-link to="/orderList"><el-button size="medium" style="width: 100px" round>取消</el-button></router-link>
      </div>
    </el-card>
  </div>
</template>

<script>
import areaSelectCmp from '@/components/area_select_cmp/index'

export default {
  name: 'ship',

  data () {
    return {
      logisticsCompany: '', // 物流公司
      logistics: '', // 货运单号
      deliverDtlData: '', // 收发信息
      logisticValue: '', // 选择的物流
      isExpress: false, // 是否显示输入物流信息的框
      receiverDeliverDtlDataEdit: 0, // 编辑收货信息状态 0显示， 1可编辑， 2编辑
      deliveryDeliverDtlDataEdit: 0, // 编辑发货信息状态 0显示， 1可编辑， 2编辑
      receiverArea: '', // 选择收货省市区
      deliveryArea: '', // 选择发货省市区
    }
  },

  components: {
    areaSelectCmp,
  },

  methods: {
    // 选择的物流方式
    onSelect (e) {
      switch (e) {
        case (0): {
          this.receiverDeliverDtlDataEdit = 1
          this.deliveryDeliverDtlDataEdit = 1
          this.isExpress = false
          this.logisticsCompany = ''
          this.logistics = ''
          break
        }
        case (1): {
          this.receiverDeliverDtlDataEdit = 1
          this.deliveryDeliverDtlDataEdit = 1
          this.isExpress = false
          this.logisticsCompany = ''
          this.logistics = ''
          break
        }
        case (2): {
          this.receiverDeliverDtlDataEdit = 0
          this.deliveryDeliverDtlDataEdit = 0
          this.isExpress = true
          break
        }
        case (3): {
          this.isExpress = false
          this.logisticsCompany = ''
          this.logistics = ''
          break
        }
      }
    },
    // 子组件传给父组件的收货区域
    onReceiverChild (e) {
      let area = e[0] + ',' + e[1]
      if (e.length === 3) {
        area = area + ',' + e[2]
        console.log(area)
      }
    },
    // 子组件传给父组件的发货区域
    onDeliveryChild (e) {
      let area = e[0] + ',' + e[1]
      if (e.length === 3) {
        area = area + ',' + e[2]
        console.log(area)
      }
    },
    // 确认发货
    onConfirm () {
      if (this.logisticValue === '') {
        return this.$message.error('请选择物流方式')
      }
      if (this.deliveryDeliverDtlDataEdit === 2 || this.receiverDeliverDtlDataEdit === 2) {
        return this.$message.error('请先保存信息')
      }
      switch (this.logisticValue) {
        case (0): {
          this.apiLogistics(0)
          break
        }
        case (1): {
          this.apiLogistics(1)
          break
        }
        case (2): {
          if (this._validationLogistics()) {
            this.apiConfirm(2)
          }
          break
        }
        case (3): {
          this.apiConfirm(3)
          break
        }
      }
    },
    // 点击保存发货信息
    onReceiverDeliverDtlDataEdit () {
      this.apiUpdateOrder('receiverDeliverDtlDataEdit')
    },
    // 点击保存收货信息
    onDeliveryDeliverDtlDataEdit () {
      this.apiUpdateOrder('deliveryDeliverDtlDataEdit')
    },

    // 私有方法--------------------------------
    // 验证物流单号和公司是否为空
    _validationLogistics () {
      if (!this.logistics) {
        this.$message.error('请输入货运单号')
        return false
      }
      if (!this.logisticsCompany) {
        this.$message.error('请输入物流公司')
        return false
      }
      return true
    },

    // api -------------------------
    // 订单编辑
    apiUpdateOrder (type) {
      let data = {
        id: this.deliverDtlData.orderId,
        sellerName: this.deliverDtlData.deliveryName,
        sellerPhone: this.deliverDtlData.deliveryPhone,
        sellerAddress: this.deliverDtlData.deliveryAddress,
        ...this.deliverDtlData
      }
      console.log('this.deliverDtlData', this.deliverDtlData)
      this.$http.post('/woho/order/head/updateOrder', data)
        .then((res) => {
          console.log('订单编辑res', res)
          if (res.data.success) {
            this.$message.success('保存成功')
            this[type] = 1
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 发送二四物流
    apiLogistics (type) {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      let data = {
        receiverPhone: this.deliverDtlData.receiverPhone,
        receiver: this.deliverDtlData.receiverName,
        receiverAddress: this.deliverDtlData.receiverAddress,
        senderPhone: this.deliverDtlData.deliveryPhone,
        sender: this.deliverDtlData.deliveryName,
        senderAddress: this.deliverDtlData.deliveryAddress,
        remarks: '备注备注',
        goodsWeight: 3,
        postType: type, // 半日达0， 即时达1
        otherType: 0,
        orderId: this.$route.query.id
      }
      this.$http.post('/woho/order/head/write24', data)
        .then((res) => {
          console.log('二四物流', res)
          if (res.data.success) {
            this.$message.success('发货成功')
            loading.close()
            this.$router.push({ path: '/orderList/orderContent', query: { id: this.$route.query.id }})
          } else {
            loading.close()
            if (res.data.msg) {
              this.$message.error(res.data.msg)
            } else {
              this.$message.error(res.data.message)
            }
          }
        }, (result) => {
          loading.close()
        })
    },
    // 确认发货
    apiConfirm (type) {
      let data = {
        logistics: this.logistics,
        logisticsCompany: this.logisticsCompany,
        orderId: this.$route.query.id,
        postType: type, // 第三方2， 自己配送3
      }
      this.$http.post('/woho/order/head/delivery', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('发货成功')
            this.$router.push({ path: '/orderList/orderContent', query: { id: this.$route.query.id }})
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 商家发货详情
    apigetDeliverDtl () {
      this.$http.get(`/woho/order/head/getDeliverDtl/${this.$route.query.id}`)
        .then((res) => {
          if (res.data.success) {
            this.deliverDtlData = res.data.data
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
  },

  created () {
    this.apigetDeliverDtl()
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .from{
      font-size: 14px;
      width: 90%;
      margin: 39px auto;
      .accept_name{
        font-weight: bold;
        margin-right: 12px;
      }
      .send_title{
        margin-top: 30px;
      }
      .send_name{
        font-weight: bold;
        margin-right: 12px;
      }
      .express_company{
        margin-top: 30px;
        .express_input{
          width: 220px;
          margin-left: 9px;
        }
      }
      .express_id{
        margin-top: 10px;
        .express_input{
          width: 220px;
          margin-left: 9px;
        }
      }
    }
    .btn_group{
      padding-top: 28px;
      width: 100%;
      text-align: center;
      border-top: solid 1px $themColor;
    }
  }
</style>
